import React, { Component } from 'react';
import { MetadataViewer } from '../Icons';
import moment from 'moment';

export default class PreviewPage extends Component {

  componentWillMount() {
    const { video } = this.props;
    this.setState({ video });
  }

  componentWillReceiveProps(props) {
    const { video } = props;
    this.setState({ video })
  }

  getDate(ms) {
    return moment.unix(ms).format('MMM Do, YYYY');
  }

  getDurationInMinutes = (seconds) => {
    return new Date(seconds * 1000).toISOString().substr(11, 8).substring(4);
  }

  render() {
    const { video } = this.state;
    if (video) {
      return (
        <div className="preview">
          <h3 className="title" onClick={this.props.handleFullScreen}>{video.title}</h3>
          <p className="description">{video.description ? video.description : 'This video does not have a description'}</p>
          <div className="summary-data">
            <p className="duration"><span className="gray">Duration:</span>{this.getDurationInMinutes(video.duration)}</p>
            <p className="uploaded"><span className="gray">Uploaded:</span>{this.getDate(video.created)}</p>
          </div>
          <div className="spinner-container">
            <div className="view-metadata" onClick={() => this.props.showMetadata(video)}><MetadataViewer /></div>
            <p className="label" onClick={() => this.props.showMetadata(video)}>View Intelligent Metadata</p>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
}



