import React, { Component } from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

// Components
import GridBox from './components/GridBox';
import PreviewPage from './components/PreviewPage';
import MetadataPage from './components/MetadataPage';
import { MetadataViewer, CloseIcon, PreviewIcon } from './components/Icons';

// Video Manifests
import { video1, video2, video3, video4, video5, video6, video7, video8, video9 } from './manifests';

export default class App extends Component {
  state = {
    view: 'grid',
    allVideos: [video1, video2, video3, video4, video5, video6, video7, video8, video9],
    backgroundImage: null
  }

  handlePreview = (video) => {
    this.setState({
      previewVideo: video,
      view: 'preview',
      backgroundImage: video.previews.filter(preview => preview.type === 'animation')[0].url
    });
  }

  clearAll = () => {
    this.setState({
      previewVideo: null,
      view: 'grid',
      backgroundImage: null
    })
  }

  addBackground = (video) => {
    const gif = video.previews.filter(preview => preview.type === 'animation')[0].url;
    const backgroundImage = gif || null;
    this.setState({ backgroundImage });
  }

  clearBackground = () => {
    this.setState({ backgroundImage: null });
  }

  getDurationInMinutes = (seconds) => {
    return new Date(seconds * 1000).toISOString().substr(11, 8).substring(4);
  }

  showMetadata = (video) => {
    const backgroundImage = video.image;
    this.setState({
      view: 'metadata',
      viewDetailVideo: video,
      backgroundImage
    })
  }

  renderGrid(style) {
    const { allVideos } = this.state;
    return (
      <ReactCSSTransitionGroup
       transitionName="fade"
       transitionAppearTimeout={0}
       transitionEnterTimeout={0}
       transitionLeaveTimeout={0}
       transitionAppear={true}>
        <div className={`grid-container ${style}`}>
          { allVideos.map((video, i) => <GridBox {...this.state} video={video} key={i} handlePreview={() => this.handlePreview(video)} addBackground={() => this.addBackground(video)} clearBackground={this.clearBackground}/>) }
        </div>
      </ReactCSSTransitionGroup>
    )
  }

  renderToolbar() {
    return (
      <div className="toolbar">
        <div className="preview-label">
          <PreviewIcon />
          <span>Thumbnail Preview</span>
        </div>
        <div onClick={this.clearAll}><CloseIcon /></div>
      </div>
    )
  }

  render() {
    const { view, previewVideo, backgroundImage } = this.state;
    return (
      <div className="App">
        <div className="main-page" style={ backgroundImage ? { backgroundImage: `url(${backgroundImage})` } : null}>
          <div className="content-wrap">
            { view === 'preview' &&
              <div>
                {this.renderToolbar()}
                <div className="flex">
                  <PreviewPage video={previewVideo} showMetadata={this.showMetadata}/>
                </div>
                {this.renderGrid('preview-style')}
              </div>
            }
            { view === 'grid' &&
            <div>
              <MetadataViewer />
              {this.renderGrid()}
            </div>
            }
            {
              view === 'metadata' &&
              <div>
                {this.renderToolbar()}
                <MetadataPage {...this.state} />
              </div>
            }
          </div>
          <div className="opacity-overlay" style={ view === 'metadata' ? { background: `rgba(0,0,0, .8)`} : { background: `rgba(0,0,0, .6)`} }></div>
        </div>
      </div>
    );
  }
}



