import React, { Component } from 'react';
import ReactJWPlayer from 'react-jw-player';

class ReactJWPlayerContainer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      video: this.props.video,
      shot: this.props.shot
    };
  }

  componentWillReceiveProps() {
    this.setState({ shot: this.props.shot })
  }

  componentWillUnmount() {
    const player = window.jwplayer(this.playerId);
    player.remove();
  }

  onReady = (event) => {
    const player = window.jwplayer(this.playerId);
    const { shot } = this.state;
    shot ? player.seek(shot.start) : false;
  }

  render() {
    const { video } = this.state;
    if (video) {
      return (
        <div className='react-jw-player-container'>
          <ReactJWPlayer
            playlist={`https://content.jwplatform.com/feeds/${video.media_id}.json`}
            onReady={this.onReady}
            playerId="player"
            playerScript='https://content.jwplatform.com/libraries/qLeYbORD.js'
          />
        </div>
      );
    } else {
      return null;
    }
  }
}

export default ReactJWPlayerContainer;



