import React, { Component } from 'react';
import { PreviewIcon } from '../Icons';

export default class GridBox extends Component {

  state = {
    video: null,
    previewIcon: false,
    backgroundImage: null
  };

  componentWillMount() {
    const { video } = this.props;
    this.setState({ video });
  }

  getDurationInMinutes = (seconds) => {
    return new Date(seconds * 1000).toISOString().substr(11, 8).substring(4);
  }

  handleMouseOver = () => {
    this.setState({ previewIcon: true });
    this.props.addBackground();
  }

  handleMouseLeave = () => {
    this.setState({ previewIcon: false });
    this.props.clearBackground();
  }

  render() {
    const { video, previewIcon } = this.state;
    const { previewVideo } = this.props;
    return (
      <div className={`grid-box ${previewVideo === video ? 'hide': ''}`}>
        <h2 className="title" onClick={this.props.handlePreview} onMouseEnter={!previewVideo ? this.handleMouseOver : null} onMouseLeave={!previewVideo ? this.handleMouseLeave : null}>{video.title}</h2>
        <div className="flex">
          <p className="duration">{this.getDurationInMinutes(video.duration)}</p>
          { previewIcon && <PreviewIcon /> }
        </div>
      </div>
    );
  }
}



